import React from 'react';
import logo from './logo.svg';
import './App.css';

import { Button } from "@blueprintjs/core";
import { Column, Table } from "@blueprintjs/table";

let webappos = window.webappos;

class MountTable extends React.Component {

  collectMountPointsRecursively(json, arr, path) { // path is with the trailing "/"
    for (let key in json) {
      if (typeof json[key] === "string") {
        arr.push({
          mountPoint: path+key,
          location: json[key]
        });
      }
      else {
        this.collectMountPointsRecursively(json[key], arr, path+key+"/");
      }
    }
    return arr;
  }

  constructor(props) {
    super(props);

    this.state = {
      fs_mount_points: [],
      supported_fs: []
    };

    let myThis = this;
    webappos.request_scopes("webappos_scopes", "login").then(()=>{
      let q = webappos.webcall_and_wait("webappos.getUserRegistryValue", "fs_mount_points");
      console.log(myThis.collectMountPointsRecursively(q.result, [], ""));
      let fs = webappos.webcall_and_wait("webappos.getSupportedFileSystems");
      console.log(fs);
    });

  }
}

function App() {


  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload.                    
        </p>
        <p>
        </p>
        <MountTable />
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
    </div>
  );
}



export default App;
